"use client"

import { useState } from "react"
import { useForm } from "antd/es/form/Form"
import { Button, Form, message } from "antd"
import { useRouter } from "next/navigation"
import { RoutesEnum } from "../../../shared/router/routesEnum"
import { RegisterCredentials } from "../types"
import { registerRequest } from "../api"
import { LeftOutlined } from "@ant-design/icons"
import { UserForm } from "./UserForm"

export const RegisterUserUI = () => {
  const [isLoading, setLoading] = useState<boolean>(false)
  const [currentForm, setCurrentForm] = useState<"userForm" | "dataForm" | "passwordForm">("userForm")
  const [form] = useForm<RegisterCredentials>()

  const router = useRouter()

  const LogInHandler = () => {
    router.push(RoutesEnum.LOGIN)
  }
  const BackButtonHandler = () => {
    router.push(RoutesEnum.LOGIN)
  }

  const successAction = async () => {
    const payload = await form.validateFields()
    setLoading(true)
    registerRequest(payload)
      .then(() => {
        router.push(RoutesEnum.HOME)
      })
      .catch(error => {
        message.error(error.message || "Ошибка регистрации")
      })
      .finally(() => {
        setLoading(false)
      })
  }

  return (
    <>
      <Form.Provider
        onFormFinish={name => {
          if (name === "passwordForm") successAction()
        }}
      >
        {currentForm === "userForm" ? (
          <UserForm />
        ) : currentForm === "dataForm" ? (
          <DataUserForm
            form={form}
            setCurrentForm={setCurrentForm}
          />
        ) : (
          <PasswordForm
            form={form}
            setCurrentForm={setCurrentForm}
          />
        )}
        <Button
          onClick={BackButtonHandler}
          htmlType='submit'
        >
          <LeftOutlined />
        </Button>
        <Button
          block
          color='default'
          onClick={LogInHandler}
          htmlType='submit'
        >
          Далее
        </Button>
      </Form.Provider>
    </>
  )
}
